@import url(https://fonts.googleapis.com/css?family=Lato);

body {
    font-family: 'Lato';
    margin: 0px;
    padding: 0px;
}
.navbar-container {
    height: 100px;
    padding-left: 32px;
    position: fixed;
    background: white;
    width: 100%;
    z-index:1;
    
}

.navbar-logo {
    width: 32px;
    height: 32px;
    padding-top: 32px;
    padding-right: 12px;
    float: left;
    vertical-align: middle;
}

.navbar-title {
    float: left;
    line-height: 100px;
    margin-left: 4px;
    margin-right: 32px;
    font-size: 18px;
}

.navbar-title-red {
    color: #F0554A;
}

.navbar-link-container {
    float: right;
    line-height: 100px;
    margin-right: 32px;

}

@media screen and (max-width: 768px) {
    .navbar-link-container {
        float: left;
        margin-bottom: 32px;
    }
}

@media screen and (max-width: 695px) {
    .navbar-link-container {
        line-height: inherit;
    }
}

.navbar-link-container > a {
    border: none;
    margin-right: 32px;
    color: black;
    text-decoration: none;
}

.navbar-link-container > a:hover {
    border-bottom: 2px solid #2C687B;
}

.navbar-background {
    width: 100%;
    height: 100px;
}
body {
    font: 16px "Lato", Regular;
}

.header-container {
    background-image: url("/fabric-website/static/media/Fabric website header.dd2503aa.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
